import { defaultProps } from '../../utils'

export default {
  props: defaultProps({
    prefixCls: 'ant-badge',
    count: Number,
    dot: false
  }),

  filters: {
    short: (value) => {
      return value >= 100 ? '99+' : value
    }
  }
}


