import { defaultProps } from '../../utils'
import vIcon from '../iconfont'

export default {
  props: defaultProps({
    prefixCls: 'ant-upload',

    items: [],
    onRemove: () => {}
  }),

  components: { vIcon },

  methods: {
    _handleClose (file) {
      this.onRemove(file)
    }
  }
}


