import { vProgressLine, vProgressCircle } from '../../src/components/progress'
import vButton, { vButtonGroup } from '../../src/components/button'
import vIcon from '../../src/components/iconfont'

export default {
  data () {
    return {
      percent: 0
    }
  },

  components: { vProgressLine, vProgressCircle, vButtonGroup, vButton, vIcon },

  methods: {
    _decline () {
      let percent = this.percent - 10;
      if (percent < 0) {
        percent = 0;
      }
      this.percent = percent
    },

    _increase () {
      let percent = this.percent + 10;
      if (percent > 100) {
        percent = 100;
      }
      this.percent = percent
    }
  }

}


