import { defaultProps } from '../../utils'
import classnames from 'classnames'

export default {
  props: defaultProps({
    className: '',
    type: {
      type: String,
      require: true
    }
  }),

  computed: {
    iconClasses () {
      return classnames({
        [this.className]: !!this.className,
        [`anticon`]: 1,
        [`anticon-${this.type}`]: this.type
      })
    }
  }
}


