module.exports = "<div class=\"markdown\"><h1 id=\"ant-design-of-vue\">Ant Design of Vue</h1>\n<hr>\n<p>这里是 Ant Design 的 Vue 实现，开发和服务于企业级后台产品。</p>\n<p><div class=\"pic-plus\">\n  <img width=\"150\" src=\"https://t.alipayobjects.com/images/rmsweb/T11aVgXc4eXXXXXXXX.svg\">\n  <span>+</span>\n  <img width=\"150\" src=\"" + require("../../styles/images/vue.png") + "\">\n</div></p>\n<style>\n.pic-plus > * {\n  display: inline-block!important;\n  vertical-align: middle;\n}\n.pic-plus span {\n  font-size: 30px;\n  color: #aaa;\n  margin: 0 20px;\n}\n</style>\n<h2 id=\"-\">特性</h2>\n<ul>\n<li>Designed as Ant Design.</li>\n<li>基于 Vue 的组件化开发模式。</li>\n<li>背靠 npm 生态圈。</li>\n<li>基于 webpack 的调试构建方案，支持 ES6。</li>\n</ul>\n<h2 id=\"-\">示例</h2>\n<h2 id=\"-\">版本</h2>\n<h2 id=\"-\">链接</h2>\n<ul>\n<li><a href=\"/vue-antd/\">首页</a></li>\n<li><a href=\"/introduce\">文档</a></li>\n<li><a href=\"/components/\">组件</a></li>\n</ul>\n<h2 id=\"-\">谁在使用</h2>\n<ul>\n<li>本人</li>\n</ul>\n<h2 id=\"-\">如何贡献</h2>\n<p>我们欢迎任何形式的贡献，有任何建议或意见您可以进行 <a href=\"https://github.com/okoala/vue-antd/pulls\">Pull Request</a>，或者给我们<a href=\"https://github.com/ant-design/ant-design/issues\">提问</a>。</p>\n</div>";


