import { defaultProps } from '../../utils'
// import Animate from './base/animate.vue'

export default {
  props: defaultProps({
    prefixCls: 'ant-alert',
    type: String,
    closable: Boolean,
    message: String,
    description: String,
    closeText: String,
    onClose: () => {}
  }),

  data () {
    return {
      iconClass: '',
      closeName: '',

      closing: true,
      closed: false
    }
  },

  computed: {
    iconClass () {
      let iconClass = this.description ? 'ant-alert-with-description-icon anticon-' : 'ant-alert-icon anticon-'

      switch (this.type) {
        case 'success':
          iconClass += 'check-circle'
          break

        case 'info':
          iconClass += 'info-circle'
          break

        case 'error':
          iconClass += 'exclamation-circle'
          break

        case 'warn':
          iconClass += 'question-circle'
          break

        default:
          iconClass += 'default'
      }

      return iconClass
    },

    closeName () {
      return !this.closing ? ' ' + this.prefixCls + '-close' : ''
    }
  },

  methods: {
    _handleClose (e) {
      let dom = this.$el
      dom.style.height = dom.offsetHeight + 'px'

      this.closing = false

      if (this.onClose) {
        this.onClose.call(this, e)
      }
    },

    _animationEnd () {
      this.closed = true,
      this.closing = true
    }
  }
}


