import Vue from 'vue'
import objectAssign from 'object-assign'
import { defaultProps, oneOfType } from '../../utils'

export default {
  props: defaultProps({
    rowSelection: Object,
    pagination: Object,
    size: 'normal',
    dataSource: oneOfType([Array, Object]),
    columns: Array,
    expandIconAsCell: true,
    onChange: () => {},

    prefixCls: 'ant-table',
    useFixedHeader: false,
    bordered: false,
    bodyStyle: Object,
    style: Object
  }),

  data () {
    return {
      selectedRowKeys: [],
      filters: {},
      loading: false,
      sortColumn: '',
      sortOrder: '',
      sorter: null
    }
  },

  ready () {
    console.log(this.dataSource)
  },

  computed: {

    isEmpty () {
      return this.dataSource && !!this.dataSource.length
    },

    tableClass () {
      let classString = this.prefixCls

      if (this.loading) {
        classString += ' ant-table-loading'
      }

      if (this.size === 'small') {
        classString += ' ant-table-small'
      }

      if (this.bordered) {
        classString += ' ant-table-bordered'
      }

      return classString
    }
  },

  ready () {
    console.log('true')
    this.compileRender()
  },

  methods: {
    compileRender () {
      const $el = this.$els.render

      for (let i = 0; i < this.columns.length; i++) {
        if (this.columns[i].render) {
          for (let j = 0; j < this.dataSource.length; j++) {
            const template = this.columns[i].render()
            const CustomRender = Vue.extend({
              template: template
            })

            const _el = $el.children[j].children[i]

            // new CustomRender().$mount(_el)
          }
          // $el
        }
      }
    }
  }
}


