import vSteps, { vStep } from '../../src/components/steps'
import vButton from '../../src/components/button'

export default {
  data () {
    return {
      steps: 3,
      currentStep: 0
    }
  },

  components: { vSteps, vStep, vButton },

  beforeCompile () {
    this.currentStep = Math.floor(Math.random() * this.steps)
  },

  methods: {
    _next () {
      let s = this.currentStep + 1
      if (s === this.steps) {
        s = 0
      }
      this.currentStep = s
    }
  }

}


