import { defaultProps, oneOf } from '../../utils'
import classnames from 'classnames'

export default {
  props: defaultProps({
    prefixCls: 'ant-spin',

    size: oneOf(['small', 'default', 'large'], 'default'),
    hide: false,
    className: String
  }),

  computed: {
    nestedStatus () {
      return this.$children ? true : false
    },

    loadingClasses () {
      return classnames({
        'ant-spin-nested-loading': !this.hide && this.nestedStatus
      })
    },

    spinWrapClasses () {
      const sizeCls = ({
        'large': 'lg',
        'small': 'sm'
      })[this.size] || ''

      return classnames({
        [`${this.prefixCls}-wrapper`]: 1,
        [`${this.prefixCls}-${sizeCls}`]: !!sizeCls
      })
    },

    spinClasses () {
      return classnames({
        [this.prefixCls]: 1,
        [this.className]: !!this.className
      })
    }
  },

  methods: {

  }
}


